@charset "UTF-8";

body,
div,
span,
header,
footer,
nav,
section,
aside,
article,
ul,
dl,
dt,
dd,
li,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
i,
b,
textarea,
button,
input,
select,
figure,
figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: inherit;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Microsoft Yahei", sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}

body:focus,
div:focus,
span:focus,
header:focus,
footer:focus,
nav:focus,
section:focus,
aside:focus,
article:focus,
ul:focus,
dl:focus,
dt:focus,
dd:focus,
li:focus,
a:focus,
p:focus,
h1:focus,
h2:focus,
h3:focus,
h4:focus,
h5:focus,
h6:focus,
i:focus,
b:focus,
textarea:focus,
button:focus,
input:focus,
select:focus,
figure:focus,
figcaption:focus {
  outline: none;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/

*::-webkit-scrollbar {
  display: none;
}

/*定义滚动条轨道 内阴影+圆角*/

/*定义滑块 内阴影+圆角*/

input[type="button"],
input[type="submit"],
input[type="search"],
input[type="reset"] {
  -webkit-appearance: none;
}

textarea {
  -webkit-appearance: none;
}

html,
body {
  width: 100%;
}

.clear:after {
  content: '';
  display: block;
  clear: both;
}

.clear {
  zoom: 1;
}

.back_img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.margin {
  margin: 0 auto;
}

.left {
  float: left;
}

.right {
  float: right;
}

.hide {
  display: none;
}

.show {
  display: block;
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.icon {
  background: no-repeat center center/100%;
  display: inline-block;
}

.mint-popup-right {
  left: 0;
  right: 0;
  bottom: auto;
  top: 0;
}

.lastPage {
  text-align: center;
  color: #222;
  line-height: 0.933333rem;
}

#app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

#app .icon-music {
  position: fixed;
  top: 1.92rem;
  left: 0.4rem;
  border-radius: 50%;
  width: 0.72rem;
  height: 0.72rem;
  background-size: 0.4rem;
  background-repeat: no-repeat;
  background-position: center;
  border: 0.026667rem solid #f5d748;
  cursor: point;
  background-image: url("./assets/music.png");
  padding: 0.133333rem;
}

#app .icon-musicP {
  background-image: url("./assets/pause.png");
}

#app .icon-pause {
  -webkit-animation: rotating 3s linear infinite;
          animation: rotating 3s linear infinite;
}

#app .pause {
  animation-play-state: paused;
  -webkit-animation-play-state: paused;
  /* Safari 和 Chrome */
}

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.backNum {
  background-image: url("./assets/numBanner.jpg");
  background-size: cover;
}

.scan {
  background: url("./assets/scan.png");
}

.result {
  background-image: url("./assets/resBack.png");
}

.resultInner {
  background-image: url("./assets/innerBack.png");
}

.result {
  background-image: url("./assets/resBack.png");
}


