.loading {
  background: #12142e;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.loading .loadinner {
  position: absolute;
  top: calc(50% - 0.226667rem);
  left: calc(0.6rem);
}

.loading .loadinner .loadLine {
  width: 8.8rem;
  height: 0.466667rem;
  border: 0.066667rem solid #84bff3;
  border-radius: 0.266667rem;
}

.loading .loadinner .loadLine .loadprocess {
  width: 0;
  border-radius: 0.266667rem;
  height: 0.333333rem;
  position: relative;
  background: #19618a;
}

.loading .loadinner .loadLine img {
  top: 0;
  position: absolute;
  width: 100%;
}

.loading .loadinner p {
  text-align: center;
}

.loading .loadinner p img {
  width: 2.8rem;
  margin-top: 0.533333rem;
}


