.loadData {
  background: #12142e;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.loadData .male {
  position: relative;
  background: url("../assets/male.png") no-repeat;
  width: 5rem;
  height: 8.32rem;
  background-size: contain;
  background-position: bottom;
  margin: 0 auto;
}

.loadData .male .topPoint {
  left: calc(50% - 0.04rem);
  top: 0.4rem;
}

.loadData .male .toppointS {
  left: calc(50% - 0.04rem);
  top: 1.333333rem;
}

.loadData .male .toppointBeside {
  left: 0.853333rem;
  top: 3.106667rem;
}

.loadData .male .toppointBesideR {
  right: 0.853333rem;
  top: 3.106667rem;
}

.loadData .male .toppomiddle {
  left: calc(50% - 0.04rem);
  top: 3.506667rem;
}

.loadData .male .topeyeR {
  top: 3.933333rem;
  right: 0.8rem;
}

.loadData .male .topeyeL {
  top: 3.933333rem;
  left: 0.8rem;
}

.loadData .male .nuisePoint {
  left: calc(50% - 0.04rem);
  top: 4.96rem;
}

.loadData .male .nuisePointR {
  right: 1.8rem;
  top: 5.266667rem;
}

.loadData .male .nuisePointL {
  left: 1.933333rem;
  top: 5.266667rem;
}

.loadData .male .mouseTop {
  left: calc(50% - 0.04rem);
  bottom: 2.4rem;
}

.loadData .male .mouseLeft {
  left: 1.866667rem;
  bottom: 2.133333rem;
}

.loadData .male .mouseRight {
  right: 1.8rem;
  bottom: 2.133333rem;
}

.loadData .male .mouseBeside {
  left: 1.573333rem;
  bottom: 1.76rem;
}

.loadData .male .mouseBesideR {
  right: 1.413333rem;
  bottom: 1.76rem;
}

.loadData .male .mouseBottomL {
  left: 2.133333rem;
  bottom: 1.64rem;
}

.loadData .male .mouseBottomR {
  right: 2rem;
  bottom: 1.64rem;
}

.loadData .male .mouseBottom {
  left: calc(50% - 0.04rem);
  bottom: 0.733333rem;
}

.loadData .scan {
  position: relative;
  background: url("../assets/scan.png");
  background-size: cover;
  width: 8.4rem;
  height: 8.32rem;
  margin: 2.133333rem auto 0;
}

.loadData .scan img {
  top: 0;
  position: absolute;
  width: 100%;
}

.loadData .bottom {
  margin-top: 0.693333rem;
  text-align: center;
}

.loadData .bottom img {
  width: 6.933333rem;
}

.flashPoint {
  background: #4896db;
  width: 0.173333rem;
  height: 0.173333rem;
  position: absolute;
  border-radius: 0.266667rem;
  display: inline-block;
}


