.home {
  background-image: url("../assets/startBanner.jpg");
  background-size: cover;
  width: 100%;
  height: 100vh;
}

.footButton {
  position: absolute;
  bottom: 1.333333rem;
}

.footButton h2 {
  opacity: 0;
  font-size: 0;
  width: 100vw;
  text-align: center;
}

.footButton h2 p {
  display: inline-block;
}

.footButton h2 .leftB span {
  display: inline-block;
  width: 0.56rem;
  height: 0.72rem;
  background-image: url("../assets/slideNext.png");
  background-size: cover;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  vertical-align: middle;
}

.footButton h2 .leftB span:nth-child(1) {
  opacity: 0.5;
}

.footButton h2 .leftB span:nth-child(2) {
  opacity: 0.8;
}

.footButton h2 .leftB span:nth-child(3) {
  opacity: 1;
}

.footButton h2 .rightB span {
  display: inline-block;
  width: 0.56rem;
  height: 0.72rem;
  background-image: url("../assets/slideNext.png");
  background-size: cover;
  vertical-align: middle;
}

.footButton h2 .rightB span:nth-child(3) {
  opacity: 0.5;
}

.footButton h2 .rightB span:nth-child(2) {
  opacity: 0.8;
}

.footButton h2 .rightB span:nth-child(1) {
  opacity: 1;
}

.footButton h2 img {
  vertical-align: middle;
  width: 5.2rem;
}

.footButton h1 {
  position: relative;
  width: 100vw;
  height: 1.253333rem;
  margin-bottom: 0.8rem;
  text-align: center;
}

.footButton h1 p {
  opacity: 0;
  line-height: 1.253333rem;
}

.footButton h1 p img {
  text-align: center;
  width: 3.813333rem;
}

.footButton h1 .animateL {
  display: inline-block;
  left: 0;
  width: 50%;
  position: absolute;
  height: 1.253333rem;
}

.footButton h1 .animateL .step1 {
  background: #e6ca45;
  width: 0;
  height: 0.066667rem;
  display: inline-block;
  left: 0;
  position: absolute;
  top: 0.6rem;
  z-index: 1;
}

.footButton h1 .animateL .step2 {
  position: absolute;
  display: inline-block;
  height: 0.066667rem;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background: #384664;
  width: 0;
  top: 0.826667rem;
  z-index: 0;
  left: 2rem;
}

.footButton h1 .animateL .step3 {
  position: absolute;
  display: inline-block;
  height: 0.066667rem;
  left: 2.533333rem;
  background: #384664;
  width: 0;
  top: 1.053333rem;
  z-index: 0;
}

.footButton h1 .animateL .step4 {
  display: none;
  position: absolute;
  display: inline-block;
  height: 0.2rem;
  left: 4.533333rem;
  background: #e6ca45;
  width: 0.2rem;
  border-radius: 50%;
  top: 0.96rem;
  z-index: 0;
  opacity: 0;
}

.footButton h1 .animateR {
  display: inline-block;
  width: 50%;
  position: absolute;
  height: 1.253333rem;
  right: 0;
}

.footButton h1 .animateR .step1 {
  background: #e6ca45;
  width: 0;
  height: 0.066667rem;
  display: inline-block;
  right: 0;
  position: absolute;
  top: 0.6rem;
  z-index: 1;
}

.footButton h1 .animateR .step2 {
  position: absolute;
  display: inline-block;
  height: 0.066667rem;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background: #384664;
  width: 0;
  top: 0.373333rem;
  z-index: 0;
  right: 2rem;
}

.footButton h1 .animateR .step3 {
  position: absolute;
  display: inline-block;
  height: 0.066667rem;
  right: 2.533333rem;
  background: #384664;
  width: 0;
  top: 0.16rem;
  z-index: 0;
}

.footButton h1 .animateR .step4 {
  position: absolute;
  display: inline-block;
  height: 0.2rem;
  right: 4.533333rem;
  background: #e6ca45;
  width: 0.2rem;
  border-radius: 50%;
  top: 0.106667rem;
  z-index: 0;
  opacity: 0;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
  }
}


