.result {
  background-image: url("../assets/resBack.png");
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.result .resultInner {
  width: 9.133333rem;
  padding: 1.333333rem;
  color: white;
  height: 14.4rem;
  opacity: 0.7;
  background-size: contain;
  margin: 1.4rem auto 0;
  background-repeat: no-repeat;
  background-image: url("../assets/innerBack.png");
}

.result .resultInner h1 {
  text-align: center;
}

.result .resultInner p {
  line-height: 1.5;
  text-indent: 2em;
}


